.homeBestSeller{width:100%;padding:30px 20px}.homeBestSeller__inner{width:100%;margin:0 auto}.homeBestSeller__title{color:#032c41;font-weight:700;font-size:16px;margin-bottom:30px}.homeBestSeller__content{width:100%}.homeBestSeller__contentCont{width:100%;display:grid;grid-template-columns:repeat(1,1fr);gap:30px}@media (min-width:320px){.homeBestSeller__contentCont{display:grid;grid-template-columns:repeat(2,1fr);gap:20px}}@media (min-width:480px){.homeBestSeller__contentCont{gap:30px}}@media (min-width:600px){.homeBestSeller{padding:30px 50px}.homeBestSeller__contentCont{gap:30px 60px}.homeBestSeller__title{font-size:20px}}@media (min-width:860px){.homeBestSeller__contentCont{grid-template-columns:repeat(3,1fr)}}@media (min-width:1080px){.homeBestSeller{padding:30px 0}.homeBestSeller__inner{max-width:1000px}.homeBestSeller__contentCont{grid-template-columns:repeat(4,1fr)}}@media (min-width:1280px){.homeBestSeller__inner{max-width:1200px}.homeBestSeller__contentCont{gap:45px}}